.button {
    width: 70px;
    height: 25px;
    border: none;
    border-radius: 5px;
}

.disabled {
    background-color: #9bc4ec;
    color: darkgrey;
}

.secondary {
    background-color: #FFFFFF;
    color: #0066cc;
    border: 2px solid #0066cc;
    transition: 0.2s;
}


.default {
    background-color: #0066cc;
    color: #FFFFFF;
    transition: 0.2s;
}


.red {
    background-color: #cc0000;
    color: #FFFFFF;
    transition: 0.2s;
}

.default:hover {
    width: 100px;
}

.red:hover {
    width: 100px;
}

.secondary:hover {
    width: 100px;
}


.default:active {
    background-color: #125394;
    color: #FFFFFF;
    transition: 0.2s;
}

.secondary:active {
    background-color: rgb(170, 215, 253);
    color: #0066cc;
    border: 2px solid #0066cc;
    transition: 0.2s;
}

.red:active {
    background-color: #9a2121;
    color: #FFFFFF;
    transition: 0.2s;
}

.button:disabled {
}
